.title {
  font-size: 35px;
  font-weight: bold;
  margin-bottom: 25px;
  margin-top: 35px;
  font-family: 'Roboto', sans-serif;
  width: 300px;
  display: flex;
  justify-content: center;
  padding: 3px;
}

.list {
  display: inline-block;
  list-style: none;
  width: 530px;
  max-height: 185px;
  padding: 0;
  overflow: auto;

  padding: 6px;
  box-sizing: border-box;
  margin-bottom: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 530px;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-size: 50;
  height: 23px;
}

.item:not(:last-child) {
  border-bottom: 1px solid grey;
  margin-bottom: 5px;
}

.deleteButton {
  border-radius: 5px;
  border: none;
  background-color: #f0f0f0;
  box-shadow: #303030 1px 1px 3px;
}

.deleteButton:hover,
.deleteButton:focus {
  background-color: #e0e0e0;
  cursor: pointer;
}
